<template>
    <div class="container">
        <div class="main__users">
            <div class="main__users-item" v-for="user in users" :key="user.id">
                <img src="@/assets/images/user.png" alt="">
                <div class="main__users-item-info">
                    <p>Name: {{ user.name }}</p>
                    <p>Username: {{ user.username }}</p>
                    <p>Email: {{ user.email }}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        created() {
            this.getUsers()
        },
        computed: {
            users() {
                return this.$store.state.users
            }
        },
        methods: {
            getUsers() {
                this.$store.dispatch('getUsers')
            }
        }
    }
</script>